import React, { useState } from 'react';
import styled from 'styled-components';
import { Icon, Button } from 'antd';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { Link } from 'react-router-dom';

const FirstIcon = styled.div`
  font-size: 3.5em;
  position: absolute;
  width: auto;
  top: 2%;
  right: 2%;
  text-align: center;
  transition: 0.5s all;
  color: white;
  cursor: pointer;
  opacity: 1;
  visibility: visible;
  @media only screen and (max-width: 500px) {
    font-size: 2.2em;
    right: 5%;
  }
`;

const Div = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  right: -100%;
  top: 0;
  background: white;
  font-size: 3em;
  text-align: center;
  transition: 1s all;
  color: white;
  z-index: 2;
  header {
    width: 100%;
    background: black;
    cursor: pointer;
  }
  div {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  h3 {
    color: lightgray;
    cursor: pointer;
    transition: 0.7s all;
  }
  h3:nth-child(2) {
    color: black;
  }
  h3:last-child {
    color: black;
  }
  @media only screen and (max-width: 500px) {
    div {
      top: 57%;
    }
    h3 {
      font-size: 0.8em;
    }
  }
`;

const CarouselDiv = styled.div`
  position: relative;
  width: 0;
  height: 0;
  background: rgba(0, 0, 0, 0.7);
  position: relative;
  z-index: 4;
  opacity: 0;
  visibility: hidden;
  transition: 0.7s all;
  h2 {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size: 1em;
  }
`;

const ReactDiv = styled.div`
  position: relative;
  width: 0;
  height: 0;
  background: rgba(0, 0, 0, 0.7);
  position: relative;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  transition: 0.7s all;
  h2 {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size: 1em;
  }
  article {
    background: white;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 70vh;
    h1 {
      font-size: 2em;
      text-align: center;
      line-height: 2;
      padding: 30px 0 0 0;
    }
  }
`;

const imageMapEtc = [
  {
    imgId: 1,
    title: 'Copy - Starbucks',
    description1: 'mainPage(responsive)',
    description2: '2019',
    description3: 'html/css/jquery',
    imgSrc: '/images/etc/1.jpg',
    pageHref: 'https://junchi86.github.io/starbucks/',
  },
  {
    imgId: 2,
    title: 'Copy - ChappelAtCheondam',
    description1: 'fullPage(responsive)',
    description2: '2019',
    description3: 'html/css/jquery/fullpage.js',
    imgSrc: '/images/etc/2.jpg',
    pageHref: 'https://junchi86.github.io/wedding/',
  },
  {
    imgId: 3,
    title: 'Copy - BasicHouse',
    description1: 'mainPage(non-responsive)',
    description2: '2019',
    description3: 'html/css/jquery/cycle.js',
    imgSrc: '/images/etc/3.jpg',
    pageHref: 'https://junchi86.github.io/basic/',
  },
  {
    imgId: 4,
    title: 'Copy - ASLE',
    description1: 'mainPage(non-responsive)',
    description2: '2019',
    description3: 'html/css/jquery/',
    imgSrc: '/images/etc/4.jpg',
    pageHref: 'https://junchi86.github.io/asle/',
  },
  {
    imgId: 5,
    title: 'Copy - FORD',
    description1: 'mainPage(non-responsive)',
    description2: '2019',
    description3: 'html/css/',
    imgSrc: '/images/etc/5.jpg',
    pageHref: 'https://junchi86.github.io/ford/',
  },
  {
    imgId: 6,
    title: 'AnimalHospital',
    description1: 'mainPage(non-responsive)',
    description2: '2019',
    description3: 'html/css/',
    imgSrc: '/images/etc/6.jpg',
    pageHref: 'https://junchi86.github.io/animalhospital/',
  },
  {
    imgId: 7,
    title: 'DogCat',
    description1: 'mainPage(non-responsive)',
    description2: '2019',
    description3: 'html/css/',
    imgSrc: '/images/etc/7.jpg',
    pageHref: 'https://junchi86.github.io/dogcat/',
  },
  {
    imgId: 8,
    title: 'Copy - Starbucks ver(before renewal)',
    description1: 'mainPage(non-responsive)',
    description2: '2019',
    description3: 'html/css/jquery',
    imgSrc: '/images/etc/8.jpg',
    pageHref: 'https://junchi86.github.io/starbucks2/',
  },
  {
    imgId: 9,
    title: 'Copy - Starbucks ver(before renewal)',
    description1: 'subPage(non-responsive)',
    description2: '2019',
    description3: 'html/css/',
    imgSrc: '/images/etc/9.jpg',
    pageHref: 'https://junchi86.github.io/starbucks3/',
  },
];

function Header() {
  const [opened, setOpened] = useState(false);
  const [vanillaOpend, setVanillaOpened] = useState(false);
  const [reactOpend, setReactOpened] = useState(false);
  const [nodeOpend, setNodeOpened] = useState(false);
  const [etcOpend, setEtcOpened] = useState(false);

  const onOpen = () => {
    opened === true ? setOpened(false) : setOpened(true);
  };
  const onVanillaOpen = () => {
    vanillaOpend === true ? setVanillaOpened(false) : setVanillaOpened(true);
  };
  const onReactOpen = () => {
    reactOpend === true ? setReactOpened(false) : setReactOpened(true);
  };
  const onNodeOpen = () => {
    nodeOpend === true ? setNodeOpened(false) : setNodeOpened(true);
  };
  const onEtcOpen = () => {
    etcOpend === true ? setEtcOpened(false) : setEtcOpened(true);
  };
  const onDup = () => {
    onReactOpen();
    onOpen();
  };

  const settings = {
    customPaging: function(i) {
      return (
        <p
          style={{
            color: 'white',
            fontSize: '1em',
            transform: 'translateY(20px)',
          }}
        >
          {i + 1}
        </p>
      );
    },
    slidesToShow: 1,
    dots: true,
    infinite: true,
    slidesToScroll: 1,
    swipeToSlide: true,
  };

  return (
    <>
      <FirstIcon
        style={
          opened
            ? { opacity: 0, visibility: 'hidden' }
            : { opacity: 1, visibility: 'visible' }
        }
        onClick={onOpen}
      >
        <Icon type="menu" />
      </FirstIcon>
      <Div style={opened ? { right: '0%' } : { right: '-100%' }}>
        <header onClick={onOpen}>
          <Icon type="caret-right" />
        </header>
        <div>
          <h3 onClick={onVanillaOpen}>Vanilla JavaScript</h3>
          <h3 onClick={onReactOpen}>React</h3>
          <h3 onClick={onNodeOpen}>Node.js</h3>
          <h3 onClick={onEtcOpen}>Etc.Pages</h3>
        </div>
      </Div>
      <ReactDiv
        style={
          reactOpend
            ? {
                opacity: 1,
                visibility: 'visible',
                width: '100%',
                height: '100vh',
              }
            : { opacity: 0, visibility: 'hidden' }
        }
      >
        <article>
          <Link to="/">
            <h1 onClick={onDup}>Momentum</h1>
          </Link>
          <Link to="/calculator">
            <h1 onClick={onDup}>Calculator</h1>
          </Link>
        </article>
        <Button
          onClick={onReactOpen}
          style={{
            fontSize: '2em',
            lineHeight: '63px',
            height: '63px',
            position: 'absolute',
            top: 0,
            borderRadius: 0,
            color: 'tomato',
          }}
          block
          type="normal"
        >
          Close
        </Button>
      </ReactDiv>
      <CarouselDiv
        style={
          etcOpend
            ? {
                opacity: 1,
                visibility: 'visible',
                width: '100%',
                height: '100vh',
              }
            : { opacity: 0, visibility: 'hidden' }
        }
      >
        <Slider
          style={{
            maxWidth: '1200px',
            height: 'auto',
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%,-50%)',
            zIndex: 5,
          }}
          {...settings}
        >
          {imageMapEtc.map(i => (
            <div style={{ position: 'relative' }} key={i.imgId}>
              <a href={i.pageHref} target="_blank" rel="noreferrer noopener">
                <h2
                  style={{
                    color: 'white',
                    cursor: 'pointer',
                  }}
                >
                  Title : {i.title} || {i.description1} || {i.description2} ||{' '}
                  {i.description3}
                </h2>

                <img
                  style={{
                    width: '100%',
                    maxHeight: '75vh',
                    boxSizing: 'border-box',
                    verticalAlign: 'middle',
                    margin: 'auto',
                    top: 0,
                    right: 0,
                    left: 0,
                    bottom: 0,
                  }}
                  src={i.imgSrc}
                  alt={i.title}
                />
              </a>
            </div>
          ))}
        </Slider>

        <Button
          onClick={onEtcOpen}
          style={{
            fontSize: '2em',
            lineHeight: '63px',
            height: '63px',
            position: 'absolute',
            top: 0,
            borderRadius: 0,
            color: 'tomato',
          }}
          block
          type="normal"
        >
          Close
        </Button>
      </CarouselDiv>
    </>
  );
}

export default Header;
