import React from 'react';
import styled from 'styled-components';
import { Button, Row, Col } from 'antd';

const Div = styled.div`
  width: 280px;
  height: 500px;
  border: 2px solid lightsteelblue;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  button {
    width: 100%;
    height: 70px;
    border-radius: 0px;
    outline: none;
    color: white;
    font-size: 1.5em;
  }
  @import '~antd/dist/antd.css';
`;
const Sol = styled.div`
  width: 100%;
  height: 150px;
  background: white;
`;
function Layout() {
  return (
    <Div>
      <Row>
        <Sol></Sol>
      </Row>
      <Row>
        <Col span={6}>
          <Button type="primary">C</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">+/-</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">%</Button>
        </Col>
        <Col span={6}>
          <Button type="danger">/</Button>
        </Col>
      </Row>
      <Row>
        <Col span={6}>
          <Button type="primary">7</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">8</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">9</Button>
        </Col>
        <Col span={6}>
          <Button type="danger">X</Button>
        </Col>
      </Row>
      <Row>
        <Col span={6}>
          <Button type="primary">4</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">5</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">6</Button>
        </Col>
        <Col span={6}>
          <Button type="danger">-</Button>
        </Col>
      </Row>
      <Row>
        <Col span={6}>
          <Button type="primary">1</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">2</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">3</Button>
        </Col>
        <Col span={6}>
          <Button type="danger">+</Button>
        </Col>
      </Row>
      <Row>
        <Col span={6}>
          <Button type="primary">0</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">.</Button>
        </Col>
        <Col span={6}>
          <Button type="primary">del</Button>
        </Col>
        <Col span={6}>
          <Button style={{ color: 'red' }} type="normal">
            =
          </Button>
        </Col>
      </Row>
    </Div>
  );
}

export default Layout;
