import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import Todo from './Todo';

const Div = styled.div`
  position: absolute;
  background: transparent;

  border-radius: 10px;
  top: 54%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1em;
  color: white;
  h1 {
    padding: 5px 25px;
    border: 0px;
    text-align: center;
    border-radius: 12px;
    font-size: 2.5em;
    color: white;
  }
  input {
    padding: 10px 0;
    border: 0px;
    border-bottom: 6px solid white;
    text-align: center;
    font-size: 2.5em;
    color: white;
    background: transparent;
    -webkit-appearance: none;
  }
  input::placeholder {
    color: white;
  }
  input:focus {
    outline: none;
  }
  input:focus::-webkit-input-placeholder {
    color: transparent;
  }
  input:focus::-moz-placeholder {
    color: transparent;
  }
  input:focus:-ms-input-placeholder {
    color: transparent;
  }
  input:focus::-ms-input-placeholder {
    color: transparent;
  }

  @media only screen and (max-width: 500px) {
    top: 35%;
    input {
      font-size: 1em;
      font-weight: bold;
      border-bottom: 2px solid white;
      width: 170px;
    }
    h1 {
      font-size: 1.3em;
      width: 200px;
      margin: 0;
      padding: 0;
      letter-spacing: 0.1em;
    }
  }
`;

function Input() {
  const [text, setText] = useState('');
  const [user, setUser] = useState(null);
  const USER = 'currentUser';
  useEffect(() => setUser(localStorage.getItem('currentUser')), []);
  const onChange = e => {
    setText(e.target.value);
  };
  const onSubmit = e => {
    e.preventDefault();
    localStorage.setItem(USER, text);
    setUser(localStorage.getItem('currentUser'));
  };

  if (user)
    return (
      <>
        <Div>
          <h1>Hello!! {user}.</h1>
        </Div>
        <Todo />
      </>
    );
  return (
    <>
      <Div>
        <form onSubmit={onSubmit}>
          <input
            type="text"
            placeholder="What's your name?"
            onChange={onChange}
            value={text}
          />
        </form>
      </Div>
    </>
  );
}

export default Input;
