import React from 'react';
import styled from 'styled-components';

const Div = styled.div`
  padding: 0;
  margin: 0;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  &::after {
    background: rgba(0, 0, 0, 0.2);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    content: '';
    position: absolute;
  }
`;
function BackGround() {
  const IMG_NUMBER = 8;
  const randomNumber = () => {
    const number = Math.floor(Math.random() * IMG_NUMBER);
    return number;
  };
  return (
    <Div
      style={{
        backgroundImage: `url(/images/${randomNumber() + 1}.jpg)`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }}
    ></Div>
  );
}

export default BackGround;
