import React, { useState, useEffect, useCallback } from 'react';
import styled from 'styled-components';

const Div = styled.div`
  position: absolute;
  top: 80%;
  left: 50%;
  height: 40%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  input {
    margin-bottom: 20px;
    padding: 0 10px;
    border: 0;
    border-bottom: 8px solid white;
    text-align: center;
    font-size: 2.5em;
    color: white;
    background: transparent;
    border-radius: 5px;
    outline: none;
    -webkit-appearance: none;
  }
  input:focus {
    outline: none;
  }
  input:focus::-webkit-input-placeholder {
    color: transparent;
  }
  input:focus::-moz-placeholder {
    color: transparent;
  }
  input:focus:-ms-input-placeholder {
    color: transparent;
  }
  input:focus::-ms-input-placeholder {
    color: transparent;
  }

  input::placeholder {
    color: white;
  }
  button {
    color: white;
  }

  @media only screen and (max-width: 500px) {
    top: 70%;
    input {
      border-bottom: 2px solid white;
      border-radius: 0;
      font-size: 1.3em;
      padding: 0;
      width: 150px;
    }
  }
`;

const ListDiv = styled.ul`
  text-align: center;
  overflow: auto;
  height: 75%;
  html {
    scrollbar-3dlight-color: #efefef;
    scrollbar-arrow-color: #dfdfdf;
    scrollbar-base-color: #efefef;
    scrollbar-face-color: #dfdfdf;
    scrollbar-track-color: #efefef;
    scrollbar-darkshadow-color: #efefef;
    scrollbar-highlight-color: #efefef;
    scrollbar-shadow-color: #efefef;
  }
  ::-webkit-scrollbar {
    width: 8px;
    height: 3px;
    border: 0px solid #fff;
    border-radius: 5px;
  }
  ::-webkit-scrollbar-track {
    background: #efefef;
    -webkit-border-radius: 10px;
    border-radius: 10px;
  }
  ::-webkit-scrollbar-thumb {
    height: 50px;
    width: 50px;
    background: rgba(0, 0, 0, 0.2);
    -webkit-border-radius: 8px;
    border-radius: 8px;
  }
`;
const List = styled.li`
  text-align: center;
  color: white;
  display: flex;
  height: auto;

  div {
    color: white;
    background: rgba(255, 82, 82, 0.4);
    margin: 5px 0;
    padding: 0 10px;
    text-align: center;
    font-size: 1.5em;
    line-height: 43px;
    border-radius: 10px;
    cursor: pointer;
  }
  h3 {
    width: 480px;
    background: rgba(0, 0, 0, 0.3);
    margin: 5px 5px 5px 0;
    text-align: center;
    font-size: 2em;
    letter-spacing: 0.25em;
    border-radius: 10px;
    color: white;
  }

  @media only screen and (max-width: 500px) {
    div {
      font-size: 1.4em;
      padding: 0;
      line-height: 1.5em;
      padding: 0 10px;
      border-radius: 5px;
    }
    h3 {
      width: 180px;
      font-size: 1.4em;
      height: 1.5em;
      margin-right: 3px;
      text-align: left;
      text-indent: 15px;
      border-radius: 5px;
    }
  }
`;
function Todo() {
  const TODO = 'todo'; // 로컬스토리지용 이름
  const initialState = [];
  const [text, setText] = useState(''); // 투두 인풋창 유즈스테이트
  const [todos, setTodos] = useState(initialState); // 투두 관리용 스테이트
  const init = () => {
    //초기화 함수
    const lsData = JSON.parse(localStorage.getItem('todo')); //로컬에서 투두 잡아오기
    if (lsData) {
      setTodos(todos.concat(lsData)); //로컬에 데이터 있으면 투두 스테이트에 결합
    } else {
      localStorage.setItem(TODO, JSON.stringify(initialState)); // 없으면 [] 로컬에 넣어주기
      return initialState;
    }
  };
  useEffect(() => {
    init();
  }, []); // 뎁스를 요청하지만 초기 한번만 실행하기 위해 안 줌.(주면 무한 로딩)

  const onChange = e => {
    // 온체인지
    setText(e.target.value);
  };

  const deleteTodos = useCallback(
    id => {
      // 삭제
      if (window.confirm('정말 삭제하시겠습니까?') === true) {
        setTodos(todos.filter((todos, index) => index !== id));
      } else {
        return false;
      }
    },
    [todos],
  );

  const addTodos = useCallback(
    text => {
      // 투두 추가
      setTodos([...todos, { todo: text }]);
    },
    [todos],
  );

  const onSubmit = useCallback(
    e => {
      // 폼 제출
      e.preventDefault();
      addTodos(text);
      setText('');
    },
    [addTodos, text],
  );

  const saveLS = useCallback(
    () => window.localStorage.setItem(TODO, JSON.stringify(todos)),
    [todos],
  ); //로컬스토리지에 투두를 저장하는 함수

  useEffect(() => {
    saveLS();
  }, [saveLS]);
  return (
    <>
      <Div>
        <form onSubmit={onSubmit}>
          <input
            type="text"
            placeholder="Todo"
            onChange={onChange}
            value={text}
          />
        </form>

        <ListDiv>
          {todos.map((i, index) => (
            <List key={index}>
              <h3>{i.todo}</h3>
              <div onClick={() => deleteTodos(index)}>V</div>
            </List>
          ))}
        </ListDiv>
      </Div>
    </>
  );
}

export default Todo;
