import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const ClockDiv = styled.div`
  position: absolute;
  top: 40%;
  left: 50%;
  text-align: center;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 4em;
  font-weight: normal;
  letter-spacing: 0.3em;
  line-height: 1.7;
  h1 {
    display: block;
    width: 100%;
    margin: 0;
    cursor: pointer;
    color: white;
  }
  @media only screen and (max-width: 500px) {
    top: 20%;
    font-size: 1.3em;
    h1 {
    }
  }
`;
const H3 = styled.h3`
  position: absolute;
  top: 0%;
  left: 1%;
  text-align: center;
  color: white;
  font-size: 1.5em;
  font-weight: normal;
  letter-spacing: 0.3em;
  line-height: 1.7;

  @media only screen and (max-width: 500px) {
    top: 5%;
    left: 50%;
    font-size: 0.75em;
    transform: translate(-50%, -50%);
  }
`;

const Clock = () => {
  const time = new Date();
  const [state, setState] = useState(new Date());
  const year = state.getFullYear();
  const day = state.getDate();
  const month = state.getMonth();
  const hour = state.getHours();
  const minutes = state.getMinutes();
  const seconds = state.getSeconds();
  const result = hour => {
    if (hour < 12) {
      return 'AM';
    } else return 'PM';
  };
  useEffect(() => {
    const interval = setInterval(() => setState(time), 1000);
    return () => {
      clearInterval(interval);
    };
  }, [time]);
  const onLogout = () => {
    if (window.confirm('로그아웃하시겠습니까?') === true) {
      localStorage.removeItem('todo');
      localStorage.removeItem('currentUser');
      window.location.reload();
    } else {
      return false;
    }
  };
  return (
    <>
      <ClockDiv>
        <h1 onClick={() => onLogout()}>
          {hour < 10 ? `0${hour}` : hour}:
          {minutes < 10 ? `0${minutes}` : minutes}
          {result(hour)}
        </h1>
      </ClockDiv>
      <Link to="/Calculator">
        <H3>
          {year}.{month + 1 < 10 ? `0${month + 1}` : month + 1}.
          {day < 10 ? `0${day}` : day}-{seconds < 10 ? `0${seconds}` : seconds}
        </H3>
      </Link>
    </>
  );
};

export default Clock;
